import React, {createContext, useEffect, useState} from 'react';
import {useWallet} from 'use-wallet';
import BnbmFinance from '../../bnbm-finance';
import config from '../../config';

export interface BnbmFinanceContext {
  bnbmFinance?: BnbmFinance;
}

export const Context = createContext<BnbmFinanceContext>({bnbmFinance: null});

export const BnbmFinanceProvider: React.FC = ({children}) => {
  const {ethereum, account} = useWallet();
  const [bnbmFinance, setBnbmFinance] = useState<BnbmFinance>();

  useEffect(() => {
    if (!bnbmFinance) {
      const bnbm = new BnbmFinance(config);
      if (account) {
        // wallet was unlocked at initialization
        bnbm.unlockWallet(ethereum, account);
      }
      setBnbmFinance(bnbm);
    } else if (account) {
      bnbmFinance.unlockWallet(ethereum, account);
    }
  }, [account, ethereum, bnbmFinance]);

  return <Context.Provider value={{bnbmFinance}}>{children}</Context.Provider>;
};
