import React, {useCallback, useEffect, useState} from 'react';
import Context from './context';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import {Bank} from '../../bnbm-finance';
import config, {bankDefinitions} from '../../config';

const Banks: React.FC = ({children}) => {
  const [banks, setBanks] = useState<Bank[]>([]);
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;

  const fetchPools = useCallback(async () => {
    const banks: Bank[] = [];

    for (const bankInfo of Object.values(bankDefinitions)) {
      if (bankInfo.finished) {
        if (!bnbmFinance.isUnlocked) continue;
        
        // only show pools staked by user
        const balance = await bnbmFinance.stakedBalanceOnBank(
          bankInfo.contract,
          bankInfo.poolId,
          bnbmFinance.myAccount,
          );
          
        if (balance.lte(0)) {
          continue;
        }
      }
      banks.push({
        ...bankInfo,
        address: config.deployments[bankInfo.contract].address,
        depositToken: bnbmFinance.externalTokens[bankInfo.depositTokenName],
        earnToken: bankInfo.earnTokenName === 'BNBM' ? bnbmFinance.BNBM : bnbmFinance.SBNB,
      });
    }

    banks.sort((a, b) => (a.sort > b.sort ? 1 : -1));
    setBanks(banks);
  }, [bnbmFinance, setBanks]);

  useEffect(() => {
    if (bnbmFinance) {
      fetchPools().catch((err) => console.error(`Failed to fetch pools: ${err.stack}`));
    }
  }, [isUnlocked, bnbmFinance, fetchPools]);

  return <Context.Provider value={{banks}}>{children}</Context.Provider>;
};

export default Banks;
