  async getBnbmStat(): Promise<TokenStat> {
    const {BnbmRewardPool, BnbmGenesisRewardPool} = this.contracts;
    const supply = await this.BNBM.totalSupply();
    const bnbmRewardPoolSupply = await this.BNBM.balanceOf(BnbmGenesisRewardPool.address);
    const bnbmRewardPoolSupply2 = await this.BNBM.balanceOf(BnbmRewardPool.address);
    const bnbmCirculatingSupply = supply.sub(bnbmRewardPoolSupply).sub(bnbmRewardPoolSupply2);
    //  const priceInBNB = await this.getTokenPriceFromPancakeswap(this.BNBM);
     //const priceInBNBstring = priceInBNB.toString();
     const priceInBTC = await this.getTokenPriceFromPancakeswapBTC(this.BNBM);
     const priceOfOneBNB = await this.getWBNBPriceFromPancakeswap();
     // const priceOfOneBTC = await this.getBTCBPriceFromPancakeswap();
    //  const priceInDollars = await this.getTokenPriceFromPancakeswapBNBMUSD();
    //  console.log('priceOfBnbmInDollars BNBM', priceInDollars);
    const priceOfBnbmInDollars = ((Number(priceInBTC) * Number(priceOfOneBNB)) / 100).toFixed(4);
    return {
      //  tokenInFtm: (Number(priceInBTC) * 100).toString(),
      tokenInFtm: priceInBTC?.toString(),
      priceInDollars: priceOfBnbmInDollars,
      totalSupply: getDisplayBalance(supply, this.BNBM.decimal, 0),
      circulatingSupply: getDisplayBalance(bnbmCirculatingSupply, this.BNBM.decimal, 0),
    };
  }

  async getBTCPriceUSD(): Promise<Number> {
    const priceOfOneBNB = await this.getBTCBPriceFromPancakeswap();
    return Number(priceOfOneBNB);
  }

  async getBNBPriceUSD(): Promise<Number> {
    const priceOfOneBNB = await this.getWBNBPriceFromPancakeswap();
    return Number(priceOfOneBNB);
  }

  /**
   * Calculates various stats for the requested LP
   * @param name of the LP token to load stats for
   * @returns
   */
  async getLPStat(name: string): Promise<LPStat> {
    const lpToken = this.externalTokens[name];
    const lpTokenSupplyBN = await lpToken.totalSupply();
    const lpTokenSupply = getDisplayBalance(lpTokenSupplyBN, 18);
    const token0 = name.startsWith('BNBM') ? this.BNBM : this.SBNB;
    const isBnbm = name.startsWith('BNBM');
    const tokenAmountBN = await token0.balanceOf(lpToken.address);
    const tokenAmount = getDisplayBalance(tokenAmountBN, 18);

    const ftmAmountBN = await this.BNB.balanceOf(lpToken.address);
    const ftmAmount = getDisplayBalance(ftmAmountBN, 18);
    const tokenAmountInOneLP = Number(tokenAmount) / Number(lpTokenSupply);
    const ftmAmountInOneLP = Number(ftmAmount) / Number(lpTokenSupply);
    const lpTokenPrice = await this.getLPTokenPrice(lpToken, token0, isBnbm);
    const lpTokenPriceFixed = Number(lpTokenPrice).toFixed(2).toString();
    const liquidity = (Number(lpTokenSupply) * Number(lpTokenPrice)).toFixed(2).toString();
    return {
      tokenAmount: tokenAmountInOneLP.toFixed(2).toString(),
      ftmAmount: ftmAmountInOneLP.toFixed(2).toString(),
      priceOfOne: lpTokenPriceFixed,
      totalLiquidity: liquidity,
      totalSupply: Number(lpTokenSupply).toFixed(2).toString(),
    };
  }

  async getLPStatBTC(name: string): Promise<LPStat> {
    const lpToken = this.externalTokens[name];
    const lpTokenSupplyBN = await lpToken.totalSupply();
    const lpTokenSupply = getDisplayBalance(lpTokenSupplyBN, 18);
    const token0 = name.startsWith('BNBM') ? this.BNBM : this.SBNB;
    const isBnbm = name.startsWith('BNBM');
    const tokenAmountBN = await token0.balanceOf(lpToken.address);
    const tokenAmount = getDisplayBalance(tokenAmountBN, 18);

    const btcAmountBN = await this.BTC.balanceOf(lpToken.address);
    const btcAmount = getDisplayBalance(btcAmountBN, 18);
    const tokenAmountInOneLP = Number(tokenAmount) / Number(lpTokenSupply);
    const ftmAmountInOneLP = Number(btcAmount) / Number(lpTokenSupply);
    const lpTokenPrice = await this.getLPTokenPrice(lpToken, token0, isBnbm);

    const lpTokenPriceFixed = Number(lpTokenPrice).toFixed(2).toString();

    const liquidity = (Number(lpTokenSupply) * Number(lpTokenPrice)).toFixed(2).toString();

    return {
      tokenAmount: tokenAmountInOneLP.toFixed(2).toString(),
      ftmAmount: ftmAmountInOneLP.toFixed(5).toString(),
      priceOfOne: lpTokenPriceFixed,
      totalLiquidity: liquidity,
      totalSupply: Number(lpTokenSupply).toFixed(2).toString(),
    };
  }
  /**
   * Use this method to get price for Bnbm
   * @returns TokenStat for BBNB
   * priceInBNB
   * priceInDollars
   * TotalSupply
   * CirculatingSupply (always equal to total supply for bonds)
   */
  async getBondStat(): Promise<TokenStat> {
    const {Treasury} = this.contracts;
    const bnbmStat = await this.getBnbmStat();

    //console.log('========= getbondstat', bnbmStat)
    const bondBnbmRatioBN = await Treasury.getBondPremiumRate();
    const modifier = bondBnbmRatioBN / 1e16 > 1 ? bondBnbmRatioBN / 1e16 : 1;
    // const modifier = 1;
    const bondPriceInBNB = (Number(bnbmStat.tokenInFtm) * modifier).toFixed(4);
    const priceOfBBnbInDollars = (Number(bnbmStat.priceInDollars) * modifier).toFixed(4);
    const supply = await this.BBNB.displayedTotalSupply();
    return {
      tokenInFtm: bondPriceInBNB,
      priceInDollars: priceOfBBnbInDollars,
      totalSupply: supply,
      circulatingSupply: supply,
    };
  }

  /**
   * @returns TokenStat for SBNB
   * priceInBNB
   * priceInDollars
   * TotalSupply
   * CirculatingSupply (always equal to total supply for bonds)
   */
  async getShareStat(): Promise<TokenStat> {
    const {CarRewardPool} = this.contracts;

    const supply = await this.SBNB.totalSupply();

    const priceInBNB = await this.getTokenPriceFromPancakeswap(this.SBNB);
    const bnbmRewardPoolSupply = await this.SBNB.balanceOf(CarRewardPool.address);
    const cabCirculatingSupply = supply.sub(bnbmRewardPoolSupply);
    const priceOfOneBNB = await this.getWBNBPriceFromPancakeswap();
    const priceOfSharesInDollars = (Number(priceInBNB) * Number(priceOfOneBNB)).toFixed(2);
    return {
      tokenInFtm: priceInBNB,
      priceInDollars: priceOfSharesInDollars,
      totalSupply: getDisplayBalance(supply, this.SBNB.decimal, 0),
      circulatingSupply: getDisplayBalance(cabCirculatingSupply, this.SBNB.decimal, 0),
    };
  }

  async getBnbmStatInEstimatedTWAP(): Promise<TokenStat> {
    const {Oracle, BnbmRewardPool} = this.contracts;
    const expectedPrice = await Oracle.twap(this.BNBM.address, ethers.utils.parseEther('100'));

    const supply = await this.BNBM.totalSupply();
    const bnbmRewardPoolSupply = await this.BNBM.balanceOf(BnbmRewardPool.address);
    const bnbmCirculatingSupply = supply.sub(bnbmRewardPoolSupply);
    return {
      tokenInFtm: getDisplayBalance(expectedPrice),
      priceInDollars: getDisplayBalance(expectedPrice),
      totalSupply: getDisplayBalance(supply, this.BNBM.decimal, 0),
      circulatingSupply: getDisplayBalance(bnbmCirculatingSupply, this.BNBM.decimal, 0),
    };
  }

