  /**
   * Calculates the TVL, APR and daily APR of a provided pool/bank
   * @param bank
   * @returns
   */
  async getPoolAPRs(bank: Bank): Promise<PoolStats> {
    if (this.myAccount === undefined) return;
    const depositToken = bank.depositToken;
    const poolContract = this.contracts[bank.contract];
    const depositTokenPrice = await this.getDepositTokenPriceInDollars(bank.depositTokenName, depositToken);
    
    const stakeInPool = await depositToken.balanceOf(bank.address);
    const TVL = Number(depositTokenPrice) * Number(getDisplayBalance(stakeInPool, depositToken.decimal));
    
    const stat = bank.earnTokenName === 'BNBM' ? await this.getBnbmStat() : await this.getShareStat();
    const tokenPerSecond = await this.getTokenPerSecond(
      bank.earnTokenName,
      bank.contract,
      poolContract,
      bank.depositTokenName,
    );
      
    const tokenPerHour = tokenPerSecond.mul(60).mul(60);
    const totalRewardPricePerYear =
      Number(stat.priceInDollars) * Number(getDisplayBalance(tokenPerHour.mul(24).mul(365)));

    const totalRewardPricePerDay = Number(stat.priceInDollars) * Number(getDisplayBalance(tokenPerHour.mul(24)));
    const totalStakingTokenInPool =
      Number(depositTokenPrice) * Number(getDisplayBalance(stakeInPool, depositToken.decimal));
    const dailyAPR = (totalRewardPricePerDay / totalStakingTokenInPool) * 100;
    const yearlyAPR = (totalRewardPricePerYear / totalStakingTokenInPool) * 100;
    return {
      dailyAPR: dailyAPR.toFixed(2).toString(),
      yearlyAPR: yearlyAPR.toFixed(2).toString(),
      TVL: TVL.toFixed(2).toString(),
    };
  }

  async getXbnbmAPR(): Promise<PoolStats> {
    if (this.myAccount === undefined) return;
    const bnbmToken = this.BNBM;
    const xbnbmToken = this.XBNBM;

    const xbnbmExchange = await this.getXbnbmExchange();
    const xbnbmPercent = await xbnbmExchange;
    const xbnbmPercentTotal = (Number(xbnbmPercent) / 1000000000000000000) * 100 - 100;

    const depositTokenPrice = await this.getDepositTokenPriceInDollars(bnbmToken.symbol, bnbmToken);

    const stakeInPool = await bnbmToken.balanceOf(xbnbmToken.address);

    const TVL = Number(depositTokenPrice) * Number(getDisplayBalance(stakeInPool, bnbmToken.decimal));

    const startDate = new Date('Febrary 14, 2022');
    const nowDate = new Date(Date.now());
    const difference = nowDate.getTime() - startDate.getTime();
    const days = difference / 60 / 60 / 24 / 1000;
    const aprPerDay = xbnbmPercentTotal / days;

    // Determine days between now and a date

    // const tokenPerHour = tokenPerSecond.mul(60).mul(60);
    // const totalRewardPricePerYear =
    //   Number(stat.priceInDollars) * Number(getDisplayBalance(tokenPerHour.mul(24).mul(365)));
    // const totalRewardPricePerDay = Number(stat.priceInDollars) * Number(getDisplayBalance(tokenPerHour.mul(24)));
    // const totalStakingTokenInPool =
    //   Number(depositTokenPrice) * Number(getDisplayBalance(stakeInPool, depositToken.decimal));
    // const dailyAPR = (totalRewardPricePerDay / totalStakingTokenInPool) * 100;
    // const yearlyAPR = (totalRewardPricePerYear / totalStakingTokenInPool) * 100;

    const dailyAPR = aprPerDay;
    const yearlyAPR = aprPerDay * 365;
    return {
      dailyAPR: dailyAPR.toFixed(2).toString(),
      yearlyAPR: yearlyAPR.toFixed(2).toString(),
      TVL: TVL.toFixed(2).toString(),
    };
  }

  /**
   * Method to return the amount of tokens the pool yields per second
   * @param earnTokenName the name of the token that the pool is earning
   * @param contractName the contract of the pool/bank
   * @param poolContract the actual contract of the pool
   * @returns
   */
  async getTokenPerSecond(
    earnTokenName: string,
    contractName: string,
    poolContract: Contract,
    depositTokenName: string,
  ) {
    //console.log('==========> debug ========>', earnTokenName,contractName, depositTokenName)
    if (earnTokenName === 'BNBM') {
      if (!contractName.endsWith('BnbmRewardPool')) {
        const rewardPerSecond = await poolContract.bnbmPerSecond();
        if (depositTokenName === 'WBNB') {
          return rewardPerSecond.mul(3000).div(9000);
        } else if (depositTokenName === 'BUSD') {
          return rewardPerSecond.mul(3000).div(9000);
        }else if (depositTokenName === 'USDC') {
          return rewardPerSecond.mul(3000).div(9000);
        }
        return rewardPerSecond;
      }
      const poolStartTime = await poolContract.poolStartTime();
      const startDateTime = new Date(poolStartTime.toNumber() * 1000);
      const FOUR_DAYS = 4 * 24 * 60 * 60 * 1000;
      //console.log('==========> debug1 ========>', earnTokenName,contractName, depositTokenName)
      if (Date.now() - startDateTime.getTime() > FOUR_DAYS) {
        return await poolContract.epochBnbmPerSecond(1);
      }
      return await poolContract.epochBnbmPerSecond(0);
    }
    const rewardPerSecond = await poolContract.cabPerSecond();
    //console.log('==========> debug2 ========>', earnTokenName,contractName, depositTokenName)
    if (!depositTokenName.startsWith('SBNB')) {
      return rewardPerSecond.mul(35500).div(59500);
    } else {
      return rewardPerSecond.mul(24000).div(59500);
    }
  }

  /**
   * Method to calculate the tokenPrice of the deposited asset in a pool/bank
   * If the deposited token is an LP it will find the price of its pieces
   * @param tokenName
   * @param pool
   * @param token
   * @returns
   */
  async getDepositTokenPriceInDollars(tokenName: string, token: ERC20) {
    let tokenPrice;
    const priceOfOneFtmInDollars = await this.getWBNBPriceFromPancakeswap();
    if (tokenName === 'WBNB') {
      tokenPrice = priceOfOneFtmInDollars;
    } else {
      if (tokenName === 'BNBM-BNB-LP') {
        tokenPrice = await this.getLPTokenPrice(token, this.BNBM, true);
      } else if (tokenName === 'BNBS-BNB-LP') {
        
        tokenPrice = await this.getLPTokenPrice(token, this.SBNB, false);
      } else if (tokenName === 'SBNB-BNB-APELP') {
        tokenPrice = await this.getApeLPTokenPrice(token, this.SBNB, false);
      } else if (tokenName === 'BNBM-BTCB-APELP') {
        tokenPrice = await this.getApeLPTokenPrice(token, this.BNBM, true);
      } else {
        tokenPrice = await this.getTokenPriceFromPancakeswap(token);
        tokenPrice = (Number(tokenPrice) * Number(priceOfOneFtmInDollars)).toString();
        
      }
    }
    return tokenPrice;
  }

