  async getHeavenAPR() {
    const Heaven = this.currentHeaven();
    const latestSnapshotIndex = await Heaven.latestSnapshotIndex();
    const lastHistory = await Heaven.heavenHistory(latestSnapshotIndex);

    const lastRewardsReceived = lastHistory[1];

    const SBNBPrice = (await this.getShareStat()).priceInDollars;
    const BNBMPrice = (await this.getBnbmStat()).priceInDollars;
    const epochRewardsPerShare = lastRewardsReceived / 1e18;

    //Mgod formula
    const amountOfRewardsPerDay = epochRewardsPerShare * Number(BNBMPrice) * 4;
    const heavencabBalanceOf = await this.SBNB.balanceOf(Heaven.address);
    const heavenTVL = Number(getDisplayBalance(heavencabBalanceOf, this.SBNB.decimal)) * Number(SBNBPrice);
    //console.log('==========>', epochRewardsPerShare, heavenTVL)
    const realAPR = ((amountOfRewardsPerDay * 100) / heavenTVL) * 365;
    return realAPR;
  }

  async getBnbmStakeAPR() {
    const Heaven = this.currentHeaven();
    const latestSnapshotIndex = await Heaven.latestSnapshotIndex();
    const lastHistory = await Heaven.heavenHistory(latestSnapshotIndex);

    const lastRewardsReceived = lastHistory[1];

    const BNBMPrice = (await this.getBnbmStat()).priceInDollars;
    const epochRewardsPerShare = lastRewardsReceived / 1e18;

    //Mgod formula
    const amountOfRewardsPerDay = epochRewardsPerShare * Number(BNBMPrice) * 4;
    const xBnbmBnbmBalanceOf = await this.BNBM.balanceOf(this.XBNBM.address);
    const bnbmTVL = Number(getDisplayBalance(xBnbmBnbmBalanceOf, this.XBNBM.decimal)) * Number(BNBMPrice);
    const realAPR = ((amountOfRewardsPerDay * 20) / bnbmTVL) * 365;
    return realAPR;
  }

  /**
   * Checks if the user is allowed to retrieve their reward from the Heaven
   * @returns true if user can withdraw reward, false if they can't
   */
  async canUserClaimRewardFromHeaven(): Promise<boolean> {
    const Heaven = this.currentHeaven();
    return await Heaven.canClaimReward(this.myAccount);
  }

  /**
   * Checks if the user is allowed to retrieve their reward from the Heaven
   * @returns true if user can withdraw reward, false if they can't
   */
  async canUserUnstakeFromHeaven(): Promise<boolean> {
    const Heaven = this.currentHeaven();
    const canWithdraw = await Heaven.canWithdraw(this.myAccount);
    const stakedAmount = await this.getStakedSharesOnHeaven();
    const notStaked = Number(getDisplayBalance(stakedAmount, this.SBNB.decimal)) === 0;
    const result = notStaked ? true : canWithdraw;
    return result;
  }

